html, body {
  margin: 0;
  padding: 0;
}

html {
  font-size: 10px;
}

body {
  color: #666666;
  line-height: 1.8;
  font-family: "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", "メイリオ", Meiryo, Osaka, "ＭＳ Ｐゴシック", "MS PGothic", sans-serif;
  font-size: 1.4rem;
  -webkit-font-smoothing: subpixel-antialiased;
  border-top: 3px solid #007837;
}

@media screen and (min-width: 768px) {
  body {
    border-top: none;
  }
}

.site-container {
  position: relative;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: bottom;
}

a:hover img {
  opacity: 0.6;
}

::selection {
  background: #bfe9cd;
  color: #000;
}

a {
  color: #333333;
  transition: 0.3s ease-out;
}

a:link, a:visited {
  text-decoration: underline;
  color: #666666;
}

a:focus, a:hover {
  text-decoration: none;
  color: #336699;
}

iframe {
  max-width: 100%;
}

table {
  border-collapse: collapse;
  border: 1px solid #DDDDDD;
}

table th,
table td {
  border: 1px solid #DDDDDD;
  padding: 2px 5px;
}

[type="text"],
[type="password"],
[type="email"],
[type="tel"],
[type="search"],
textarea {
  border: 1px solid #CCCCCC;
  padding: 2px 5px;
}

/* ------------------------------
2. Layout
------------------------------ */
/* header layout */
.header__inner {
  position: relative;
  max-width: 960px;
  margin: auto;
  height: 60px;
  padding-left: 12px;
  padding-right: 12px;
}

@media screen and (min-width: 768px) {
  .header__inner {
    padding-left: 0;
    padding-right: 0;
    height: auto;
  }
}

/* header logo */
.header__logo {
  margin: 0;
  padding: 0;
  height: 60px;
  line-height: 60px;
}

.header__logo img {
  max-height: 40px;
  width: auto;
  vertical-align: middle;
}

@media screen and (min-width: 768px) {
  .header__logo {
    height: auto;
    max-height: none;
    padding: 20px 0;
    line-height: 1;
  }
}

/* header description */
.site-description {
  display: none;
  background-color: #000000;
  color: #FFFFFF;
  padding: 5px 0;
}

.site-description__text {
  max-width: 960px;
  margin: auto;
  font-size: 1.3rem;
}

@media screen and (min-width: 768px) {
  .site-description {
    display: block;
  }
}

/* header navigation layout */
.slicknav_nav,
.main-nav {
  display: none;
  position: absolute;
  z-index: 1;
  width: 100%;
  border-top: 0;
  border-bottom: 0;
  background-color: rgba(32, 32, 32, 0.8);
  color: #FFFFFF;
}

@media screen and (min-width: 768px) {
  .slicknav_nav,
  .main-nav {
    display: block;
    position: static;
    border-top: 1px solid #CCCCCC;
    border-bottom: 1px solid #CCCCCC;
    background-color: #FFFFFF;
  }
}

.slicknav_nav a,
.main-nav a {
  display: block;
  height: 100%;
  padding: 1.5rem 10px 1.5rem;
  text-decoration: none;
  color: #FFFFFF;
}

.slicknav_nav a:hover, .slicknav_nav a:focus, .slicknav_nav a:active,
.main-nav a:hover,
.main-nav a:focus,
.main-nav a:active {
  background-color: #222222;
}

@media screen and (min-width: 768px) {
  .slicknav_nav a,
  .main-nav a {
    background-color: #FFFFFF;
    color: #666666;
  }
  .slicknav_nav a:hover, .slicknav_nav a:focus,
  .main-nav a:hover,
  .main-nav a:focus {
    background-color: #F0F0F0;
  }
}

.slicknav_nav-list,
.main-nav-list {
  width: 100%;
  margin: auto;
  padding: 0;
  list-style: none;
}

@media screen and (min-width: 768px) {
  .slicknav_nav-list,
  .main-nav-list {
    display: table;
    table-layout: fixed;
    max-width: 960px;
  }
}

.slicknav_nav .menu-item, .slicknav_nav-list__item,
.main-nav .menu-item,
.main-nav-list__item {
  float: left;
  width: 50%;
  border-color: #999999;
  border-style: solid;
  border-width: 0 0 1px 0;
}

.slicknav_nav .menu-item:nth-child(odd), .slicknav_nav-list__item:nth-child(odd),
.main-nav .menu-item:nth-child(odd),
.main-nav-list__item:nth-child(odd) {
  border-right-width: 1px;
}

@media screen and (min-width: 768px) {
  .slicknav_nav .menu-item, .slicknav_nav-list__item,
  .main-nav .menu-item,
  .main-nav-list__item {
    float: none;
    width: auto;
    vertical-align: middle;
    display: table-cell;
    text-align: center;
    border-color: #CCCCCC;
    border-right-width: 1px;
    border-bottom-width: 0;
  }
  .slicknav_nav .menu-item:first-child, .slicknav_nav-list__item:first-child,
  .main-nav .menu-item:first-child,
  .main-nav-list__item:first-child {
    border-left-width: 1px;
  }
  .slicknav_nav .menu-item.current-menu-item a,
  .slicknav_nav .menu-item--active a, .slicknav_nav-list__item.current-menu-item a,
  .slicknav_nav-list__item--active a,
  .main-nav .menu-item.current-menu-item a,
  .main-nav .menu-item--active a,
  .main-nav-list__item.current-menu-item a,
  .main-nav-list__item--active a {
    border-top: 0.3rem solid #007837;
    padding-top: 1.2rem;
  }
}

/* header SP slick nav (jQuery plugin) */
.slicknav_menu {
  display: block;
}

@media screen and (min-width: 768px) {
  .slicknav_menu {
    display: none;
  }
}

.slicknav_btn {
  padding: 10px;
  position: absolute;
  z-index: 100;
  top: 12.5px;
  right: 5px;
}

.slicknav_menutxt {
  display: none;
}

.slicknav_icon-bar {
  transition: all .15s ease-out;
  display: block;
  width: 20px;
  height: 3px;
  margin-bottom: 3px;
  background-color: #4D4D4D;
}

.slicknav_icon-bar:last-child {
  margin-bottom: 0;
}

.slicknav_nav {
  position: absolute;
  top: 60px;
}

/* SP nav button ≡ -> × */
.slicknav_open .slicknav_icon-bar:first-child {
  transform: translateY(6px) rotate(45deg);
}

.slicknav_open .slicknav_icon-bar:nth-child(2) {
  opacity: 0;
}

.slicknav_open .slicknav_icon-bar:last-child {
  transform: translateY(-6px) rotate(-45deg);
}

/* PC fixed nav */
@media screen and (min-width: 768px) {
  .is-nav-fixed {
    padding-top: 5.7rem;
  }
  .is-nav-fixed .main-nav {
    position: fixed;
    top: 0;
  }
}

/* main container */
.body {
  margin: 0 auto 40px;
}

/* page-title */
.page-header {
  display: flex;
  flex-direction: column-reverse;
  margin-bottom: 40px;
  padding: 10px 0 10px;
  background-color: #007837;
}

.page-header__text {
  width: 100%;
  max-width: 960px;
  background-color: #007837;
  color: #FFFFFF;
  margin: 0 auto;
  padding: 0 5px;
  font-family: "ヒラギノ明朝 ProN W3", "Hiragino Mincho ProN", "HG明朝E", "ＭＳ Ｐ明朝", "ＭＳ 明朝", serif;
  font-weight: normal;
}

.page-header__text--main {
  font-size: 2.0rem;
  line-height: 1.4;
}

@media screen and (min-width: 768px) {
  .page-header__text--main {
    font-size: 4.0rem;
  }
}

.page-header__text--sub {
  font-size: 1.2rem;
}

@media screen and (min-width: 768px) {
  .page-header__text--sub {
    font-size: 1.6rem;
  }
}

/* contents */
.page-content {
  max-width: 960px;
  margin: auto;
  padding: 0 10px;
}

.page-content::after {
  clear: both;
  content: "";
  display: block;
}

@media screen and (min-width: 768px) {
  .page-content {
    padding: 0;
  }
}

section.border {
  border-bottom: 1px solid #DDDDDD;
  padding-bottom: 20px;
  margin-bottom: 20px;
}

section.border + section > :first-child {
  margin-top: 0;
}

/* home - key visual */
.key-visual {
  margin-bottom: 20px;
}

@media screen and (min-width: 768px) {
  .key-visual {
    margin-bottom: 40px;
  }
}

.key-visual--pc {
  display: none;
}

@media screen and (min-width: 768px) {
  .key-visual--pc {
    display: block;
    height: 300px;
    background-position: 50% 0;
  }
  .key-visual--pc img {
    visibility: hidden;
  }
}

.key-visual--sp {
  display: block;
  text-align: center;
}

.key-visual--sp img {
  display: block;
  width: 100%;
  height: auto;
}

@media screen and (min-width: 768px) {
  .key-visual--sp {
    display: none;
  }
}

/* footer */
.footer {
  clear: both;
  margin: 0 auto;
  padding: 20px 0;
  text-align: center;
  background-color: #f2f2f2;
  border-top: 1px solid #e1e1e1;
}

.footer p {
  text-align: center;
  margin: 0 auto;
  color: #999;
}

/* ------------------------------
3. Module - Component
------------------------------ */
/* attention */
.attention {
  border: 4px solid #ddd;
  margin: 0 auto 20px;
  padding: 15px;
}

.attention--home {
  width: 96%;
  max-width: 600px;
}

.attention__title {
  margin-top: 0;
  margin-bottom: 0;
}

.attention__title--important {
  font-size: 1em;
  line-height: 1.8;
  font-weight: bold;
  color: #c00;
}

.attention__title + * {
  margin-top: 0;
}

.attention > :last-child {
  margin-bottom: 0;
}

/* button */
.button {
  position: relative;
  display: inline-block;
  max-width: 100%;
  font-size: 1.4rem;
  line-height: 1;
  cursor: pointer;
  text-decoration: none;
  text-align: center;
  background-color: transparent;
  color: #666666;
  border: 1px solid #DDDDDD;
  border-radius: 0;
  padding-top: 0.75em;
  padding-bottom: 0.75em;
  padding-top: calc(0.75em - 1px);
  padding-bottom: calc(0.75em - 1px);
  padding-left: 1em;
  padding-right: 1em;
  font-weight: normal;
  transition: border-color .15s linear, background-color .15s linear, color .15s linear;
}

.button:link, .button:active {
  text-decoration: none;
}

.button:focus, .button:hover {
  background-color: #f0f0f0;
  outline: 0;
}

.button--block {
  display: block;
  width: 100%;
}

.button--primary {
  border-color: #007837;
  background-color: #007837;
  color: #FFFFFF;
}

.button--primary:link, .button--primary:visited {
  color: #FFFFFF;
}

.button--primary:hover, .button--primary:focus {
  border-color: #00c55a;
  background-color: #00c55a;
}

.button--large {
  font-size: 2rem;
}

.button--small {
  font-size: 1.2rem;
}

/* form */
.input--block {
  display: block;
  width: 100%;
}

.label {
  display: inline-block;
  border-radius: 4px;
  padding: .15em .75em;
  font-size: 1.2rem;
}

.label--required {
  background-color: red;
  color: #FFFFFF;
}

.label--optional {
  background-color: blue;
  color: #FFFFFF;
}

form th .label {
  margin-left: .5em;
}

/* heading */
h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
  line-height: 1.4;
}

h1, .h1 {
  font-size: 3rem;
}

h2, .h2 {
  color: #007837;
  margin-top: 2em;
  font-size: 1.8rem;
  font-weight: bold;
}

@media screen and (min-width: 768px) {
  h2, .h2 {
    font-size: 2.6rem;
    font-weight: normal;
  }
}

h2 > .cap, .h2 > .cap {
  font-size: 1.0rem;
  color: #666666;
  margin-left: 15px;
}

@media screen and (min-width: 768px) {
  h2 > .cap, .h2 > .cap {
    font-size: 1.6rem;
  }
}

h3, .h3 {
  font-size: 1.8rem;
  font-weight: bold;
}

@media screen and (min-width: 768px) {
  h3, .h3 {
    font-size: 2.6rem;
    font-weight: normal;
  }
}

h4, .h4 {
  font-size: 1.8rem;
  font-weight: bold;
}

@media screen and (min-width: 768px) {
  h4, .h4 {
    font-size: 2.2rem;
    font-weight: normal;
  }
}

h5, .h5 {
  font-size: 1.8rem;
  font-weight: bold;
}

@media screen and (min-width: 768px) {
  h5, .h5 {
    font-size: 2.0rem;
    font-weight: normal;
  }
}

h6, .h6 {
  font-size: 1.8rem;
  font-weight: bold;
}

@media screen and (min-width: 768px) {
  h6, .h6 {
    font-size: 1.8rem;
    font-weight: normal;
  }
}

/* pagination (wp the_posts_pagination) */
.pagination {
  margin: 40px 0;
}

.pagination .screen-reader-text {
  display: none;
}

.pagination .nav-links {
  text-align: center;
}

.pagination .page-numbers {
  display: inline-block;
  padding: .25em .75em;
  border: 1px solid #DDDDDD;
}

.pagination .page-numbers.current {
  font-weight: bold;
}

/* table */
.table--border {
  border-width: 1px;
}

.table--border th,
.table--border td {
  border-width: 1px;
}

.table--full {
  width: 100%;
}

/* in form */
form table {
  border: 1px solid #DDDDDD;
}

form table th,
form table td {
  display: block;
  padding: 3px;
  border: 1px solid #DDDDDD;
}

@media screen and (min-width: 480px) {
  form table th,
  form table td {
    display: table-cell;
  }
}

form table th {
  background-color: #f1f1f1;
  text-align: left;
  font-weight: normal;
}

form table td {
  background-color: #fafafa;
}

/* ------------------------------
4. Module - Utility
------------------------------ */
/* WordPress */
.alignleft {
  float: left;
  margin-right: 1.5em;
}

.alignright {
  float: right;
  margin-left: 1.5em;
}

.aligncenter {
  clear: both;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

/* clearfix */
.cf::after,
.clearfix::after {
  clear: both;
  content: "";
  display: block;
}

